<template>
<div id="root">
  <form action="" id="input">
    <el-input size="medium" v-model="val" placeholder="请输入内容"></el-input>
    <br><br>
    <el-button size="" @click="generateGraph()" type="success">查询</el-button>
    <hr>
  </form>
<div id="graph"></div>
</div>
</template>

<script>
import G6 from '@antv/g6'
export default {
  name: "WordGraph",
  data(){
    return{
      graphData: {
        nodes: [],
        edges: []
      },
      val: ''
    }
  },
  methods: {
    generateGraph(){
      $.ajax({
        url: 'https://project.midsummra.com/api/generateGraph',
        type: 'POST',
        dataType: 'JSON',
        data: {
          val: this.val
        },
        success:(resp)=>{
          console.log(resp)
          if (resp.data.code === 0){
            this.$message({
              showClose: true,
              message: '查询图谱失败>A<',
              type: 'error'
            })
          }else {
            this.$message({
              showClose: true,
              message: '图谱生成成功>w<',
              type: 'success'
            })
            this.graphData.nodes = resp.data[0]
            this.graphData.edges = resp.data[1]
          }
        },
        error:()=>{
          this.$message({
            showClose: true,
            message: '发生未知错误;w;',
            type: 'error'
          })},
        complete:()=>{
          $("#graph").empty();
          if (!this.graphData.nodes.isEmpty){
            this.graphData.nodes.forEach((node)=>{
              if(!node.style){
                node.style = {}
              }
              switch (node.class){
                case 'word': {
                  node.size = 50
                  node.style.fill = '#f0f8ff'
                  break
                }
                case 'translation': {
                  node.size = 55
                  node.style.fill = 'lightblue'
                  break
                }
                case 'source': {
                  node.size = 80
                  node.style.fill = '#FFB7C5'
                  break
                }
              }
            })
            var graph = new G6.Graph({
              fitView: true,
              fitViewPadding: [20, 40, 50, 20],
              container: 'graph',
              width: 3000,
              height: 2000,
              defaultNode: {
                size: 50,
              },
              defaultEdge: {
                type: "line",
                style: {
                  endArrow: true,
                  stroke: '#ffb8c6',
                  lineWidth: 1.5,
                  strokeOpacity: 10
                }
              },
              layout: {
                type: 'force2',
                animate: false,
                center: [0,0],
                linkDistance: 120,
                nodeStrength: node=>{
                  if (node.class === 'word'){
                    return 600
                  }else {
                    return 1000
                  }
                },
                preventOverlap: true,
                nodeSize: 60
              },
              modes: {
                default: ['drag-canvas', 'zoom-canvas', 'drag-node',
                  {
                    type: 'activate-relations',
                    trigger: 'click'
                  }
                ], // 允许拖拽画布、放缩画布、拖拽节点...
              },
            })
            graph.on('afteractivaterelations', (e) => {
              console.log(e.item);
              console.log(e.action);
            });
            graph.data(this.graphData)
            graph.render()
          }
        },

      })
    }
  },
  mounted() {
    $("#graph").empty();
  }
}
</script>

