<template>
  <div id="root">
  <div v-show="erratumIsNull">
    <h2>勘误为空或发生未知错误</h2>
  </div>
  <div id="root">
    <h2>勘误表</h2>
    <h3>共{{count}}条数据</h3>
    <h3>共{{count}}条数据/最后更新于{{latest}}</h3>
    <hr>
    <el-table
        :data="erratum"
        stripe="true"
        border="true"
        style="width: 100%">
      <el-table-column
          prop="id"
          label="id"
          width="220">
      </el-table-column>
      <el-table-column
          prop="date"
          label="日期"
          width="300"
         >
      </el-table-column>
      <el-table-column
          prop="word"
          label="词条"
          width="239">
      </el-table-column>
      <el-table-column
          prop="translation"
          label="翻译"
          width="220">
      </el-table-column>
      <el-table-column
          prop="reason"
          label="原因"
          width="300">
      </el-table-column>
      <el-table-column
          label="操作"
          width="220">
        <template slot-scope="scope">
          <el-button size="mini" @click="modifyTransl(scope)">修改释义</el-button>
          <el-button size="mini" type="danger" @click="deleteTransl(scope)">删除释义</el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
    <hr>
  </div>
</template>

<script>
import axios from "axios";
import QS from 'qs'
export default {
  name: "ErratumList",
  data(){
    return{
      erratum:{},
      erratumIsNull: false,
      count: 0,
      latest: 0
    }
  },
  computed:{

  },
  methods:{
    deleteTransl(scope){
      let isConfirm = confirm('您是否要删除['+scope.row.word+']的释义['+scope.row.translation+']')
      if (isConfirm){
        axios.post('https://project.midsummra.com/admin/deleteTranslation',QS.stringify({
          id: scope.row.id,
          word: scope.row.word,
          translation: scope.row.translation,
        }))
            .then((resp)=>{
              if (resp.data.code !== 0){
                this.$message({
                  showClose: true,
                  message: '删除释义失败['+resp.data.message+']',
                  type: 'error'
                })
              }else {
                this.erratum.splice(scope.$index,1)
                this.$message({
                  showClose: true,
                  message: '删除释义成功',
                  type: 'success'
                })
              }
            })
      }
    },
    modifyTransl(scope){
      let newTranslation = prompt('请输入修改后的释义')
      if (newTranslation !== null){
        axios.post('https://project.midsummra.com/admin/updateTranslation',QS.stringify({
          id: scope.row.id,
          word: scope.row.word,
          translation: scope.row.translation,
          newTranslation: newTranslation
        }))
            .then((resp)=>{
              if (resp.data.code !== 0){
                console.log(resp.data)
                this.$message({
                  showClose: true,
                  message: '修改释义失败['+resp.data.message+']',
                  type: 'error'
                })
              }else {
                this.erratum.splice(scope.$index,1)
                this.$message({
                  showClose: true,
                  message: '修改释义成功',
                  type: 'success'
                })
              }
            })

      }
    },
  },
  mounted() {
    axios.get('https://project.midsummra.com/admin/erratumInfo')
        .then((resp)=>{
          if (resp.data.code === 0 && resp.data.data === null){
            this.erratumIsNull = false
          }else if (resp.data.code === 0){
            this.erratum = resp.data.data
            this.count = this.erratum.length
            this.latest = this.erratum[this.erratum.length-1].date
          }else {
            this.erratumIsNull = true
          }
        })
  }
}
</script>

<style scoped>
 #root{
   width: 1500px;
   margin-right: auto;
 }
</style>