<template>
  <div id="root">
    <div v-show="sourceIsNull">
      <h2>来源为空或发生未知错误</h2>
    </div>
    <div id="root">
      <h2>来源统计表</h2>
      <h3>共{{count}}条数据/最后更新于{{latest}}</h3>
      <hr>
      <el-table
          :data="source"
          stripe="true"
          border="true"
          style="width: 100%">
        <el-table-column
            prop="id"
            label="id"
            width="199">
        </el-table-column>
        <el-table-column
            prop="date"
            label="日期"
            width="300"
        >
        </el-table-column>
        <el-table-column
            prop="translation"
            label="翻译"
            width="220">
        </el-table-column>
        <el-table-column
            prop="source"
            label="出处"
            width="220">
        </el-table-column>
        <el-table-column
            prop="url"
            label="出处url"
            width="320">
          <template slot-scope="scope">
            <el-popover trigger="hover" placement="top">
              <p>{{ deserializationURL(scope.row.url) }}</p>
              <div slot="reference" class="name-wrapper">
                <el-tag size="medium">{{ scope.row.url }}</el-tag>
              </div>
            </el-popover>
          </template>
        </el-table-column>
        <el-table-column
            label="操作"
            width="240">
          <template slot-scope="scope">
            <el-button size="mini" @click="submitSource(scope,null)" type="success">认可出处</el-button>
            <el-button size="mini" @click="submitSource(scope,scope.row.url)" type="success">认可出处与URL</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <hr>
  </div>
</template>

<script>
import axios from "axios";
import QS from 'qs';

export default {
  name: "SourceList",
  data(){
    return{
      source:{},
      sourceIsNull: false,
      count: 0,
      latest: 0
    }
  },
  methods:{
    deserializationURL(url){
      if (url === null){
        return '----url为空----'
      }else {
        return url.replaceAll('_','/').replaceAll('-','.')
      }
    },
    submitSource(scope,url){
      let isConfirm = confirm('您是否要认可['+scope.row.translation+']的出处['+scope.row.source+']及其url['+url+']')
      if (isConfirm){
        axios.post('https://project.midsummra.com/admin/submitSource',QS.stringify({
          id: scope.row.id,
          translation: scope.row.translation,
          source: scope.row.source,
          url: url
        }))
            .then((resp)=>{
              if (resp.data.code !== 0){
                this.$message({
                  showClose: true,
                  message: '提交出处失败['+resp.data.message+']',
                  type: 'error'
                })
              }else {
                this.source.splice(scope.$index,1)
                this.$message({
                  showClose: true,
                  message: '提交出处成功',
                  type: 'success'
                })
              }
            })
      }

    }
  },

  mounted() {
    axios.get('https://project.midsummra.com/admin/sourceInfo')
        .then((resp)=>{
          if (resp.data.code === 0 && resp.data.data === null){
            this.sourceIsNull = false
          }else if (resp.data.code === 0){
            this.source = resp.data.data
            this.count = this.source.length
            this.latest = this.source[this.source.length-1].date
          }else {
            this.sourceIsNull = true
          }
        })
  }
}
</script>

<style scoped>
#root{
  width: 1500px;
  margin-right: auto;
}
</style>