<template>
<div>
  <hr>
  <h2>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;添加释义</h2>
  <el-form ref="form" label-width="80px" :rules="rules" :model="form">
    <el-form-item label="词语" class="param" prop="notNull">
      <el-input v-model="form.word" style="width: 50%"></el-input>
    </el-form-item>
    <el-form-item label="释义" class="param" prop="notNull">
      <el-input v-model="form.translation" style="width: 50%"></el-input>
    </el-form-item>
    <el-form-item label="来源" class="param">
      <el-input v-model="form.source" placeholder="非必需，可不填" style="width: 50%"></el-input>
    </el-form-item>
    <el-form-item label="URL" class="param">
      <el-input v-model="form.url" placeholder="非必需，可不填" style="width: 50%"></el-input>
    </el-form-item>
    <div id="button">
      <el-button type="primary" @click="submitTransl">添加释义</el-button>
      <el-button type="danger" @click="clearForm">清空表单</el-button>
    </div>
  </el-form>
  <hr>
</div>
</template>

<script>
import axios from "axios";
import QS from 'qs'

export default {
  name: "AddTranslationForm",
  data(){
    return{
      form: {
        word: '',
        translation: '',
        source:  '',
        url: '',
      },
      rules: {
        notNull: [
          // {required: true, message: '该栏不能为空', trigger: 'blur'},
          // { min: 3, max: 5, message: '长度在 3 到 5 个字符', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    submitTransl(){
      if ((this.form.word === null || this.form.word === '') || (this.form.translation === null || this.form.translation === '')){
        alert('词条或释义为空！')
      }else {
        axios.post('https://project.midsummra.com/admin/submitTranslToPS',QS.stringify({
          word: this.form.word,
          translation: this.form.translation,
          source: this.form.source,
          url: this.form.url
        }))
            .then(resp=>{
              if (resp.data.code === 0){
                this.$message({
                  showClose: true,
                  message: '提交释义成功',
                  type: 'success'
                })
              }else {
                this.$message({
                  showClose: true,
                  message: '修改释义失败['+resp.data.message+']',
                  type: 'error'
                })
              }
            })
      }
    },
    clearForm(){
      this.form.url = ''
      this.form.word = ''
      this.form.translation = ''
      this.form.source = ''
    }
  }
}
</script>

<style scoped>
#button{
  margin: 0 50px;
}
.param{
  font-size: large;
}
</style>